const API_URL = "https://awcu2nks23.us-east-1.awsapprunner.com";
const load = async ({ params }) => {
  const response = await fetch(`${API_URL}/api/${params.event}/bracket`);
  if (response.status == 404) {
    return {
      games: null
    };
  }
  const data = await response.json();
  return {
    event: data.event,
    rounds: data.rounds,
    courts: data.courts,
    root: data.root
  };
};
export {
  load
};
